<template>
  <h2>我是About的内容</h2>
</template>

<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "About",

  // 通过路由规则，进入该组件时被调用
  beforeRouteEnter(to,from,next){
    console.log('beforeRouteEnter---进入About组件了')
    console.log('组件内进入路由守卫',to,from,next)
    if (to.meta.isLimit){ //判断是否需要鉴权
      if (localStorage.getItem('school') === 'atguigu'){
        next()
      }else {
        alert('无权限查看，学校名必须为atguigu')
      }
    }else {
      next()
    }
  },

  // 通过路由规则，离开该组件时被调用
  beforeRouteLeave(to,from,next){
    console.log('beforeRouteLeave---离开About组件了')
    next()
  }


}
</script>

