//该文件主要用于创建整个应用的路由器
import VueRouter from "vue-router";
import About from "@/components/About";
import Home from "@/components/Home";
import News from "@/components/News";
import Message from "@/components/Message";
import Detail from "@/components/Detail";

//创建并暴露一个路由器
const router = new VueRouter({
    routes:[
        {
            name:'guanyu', //给路由命名
            path:'/about',
            component:About,
            meta:{isLimit:true,title:'关于'}
        },
        {
            name:'zhuye',
            path:'/home',
            meta:{isLimit:false,title:'主页'},
            component:Home,
            children:[
                {
                    name:'xinwen',
                    path:'news',
                    meta:{isLimit:true,title:'新闻'},
                    component:News,
                    // 路由独享守卫
                    /*beforeEnter(to,from,next){
                        console.log('路由独享守卫',to,from,next)
                        if (to.meta.isLimit){ //判断是否需要鉴权
                            if (localStorage.getItem('school') === 'atguigu'){
                                next()
                            }else {
                                alert('无权限查看，学校名必须为atguigu')
                            }
                        }else {
                            next()
                        }
                    }*/
                },
                {
                    name:'xinxi',
                    path:'message',
                    meta:{isLimit:true,title:'信息'},
                    component:Message,
                    children:[
                        {
                            name:'xiangqing', //给路由命名
                            path:'detail', //使用占位符声明接收params参数
                            component:Detail,

                            // 第一种写法:props值为对象，改对象中所有的key-value的组合最终都会通过props传给Detail组件
                            // props:{a:1,b:'hello'},

                            //第二种写法:props值为布尔值，为true时，则把路由收到的所有params参数通过props传递给Detail组件
                            // props:true

                            // 第三种写法:props值为函数,该函数返回的对象中每一组key-value都会通过props传给Detail组件
                            props($route){
                                return{
                                    id:$route.query.id,
                                    title:$route.query.title
                                }
                            }
                        }
                    ]
                }
            ]
        }
    ]
})

//全局前置路由守卫————初始化的时候被调用、每次路由切换之前被调用
/*router.beforeEach((to, from, next)=>{
    console.log('全局前置路由守卫',to,from,next)
    // 第一种判断方式：if (to.name ==='xinxi' || to.name ==='xinwen'){
    // 第二种判断方式：通过在需要鉴权的路由中配置字段，这里是isLimit
    if (to.meta.isLimit){ //判断是否需要鉴权
        if (localStorage.getItem('school') === 'atguigu'){
            next()
        }else {
            alert('无权限查看，学校名必须为atguigu')
        }
    }else {
        next()
    }
})*/

//全局后置路由守卫————初始化的时候被调用、每次路由切换之后被调用
router.afterEach((to, from)=>{
    console.log('全局后置路由守卫',to,from)
    document.title = to.meta.title || '硅谷系统'
})

export default router
